@import "mediaq";


$phone: "(min-width: 576px) and (max-width: 767px)";
$tablet: '(min-width: 768px) and (max-width: 1023px))';
$desktop: "(min-width: 1024px)";

$TMyellow: #F2DF74;
$TMblue: #004165;
$TMgray: #A9B2B1;
$TMred: #CD202C;
$TMpurple: #772432;

@mixin coloredbutton($color) {
   transition: background 0.8s, color 0.8s;
   &:hover {
      color: white;
      opacity: 100;
      background: lighten($color, 10%) radial-gradient(circle, transparent 1%, lighten($color, 10%) 1%) center/15000%; }
   &:active {
      opacity: 100;
      background-color: lighten($color, 20%);;
      background-size: 100%;
      transition: background 0s; } }

.TMblue {
  color: $TMblue;
  background-color: white;
  @include coloredbutton($TMblue); }

.DisabledButton {
  color: black;
  background-color: $TMgray;
  pointer-events: none; }


.TMyellow {
   color: $TMyellow;
   background-color: white;
   @include coloredbutton($TMyellow); }

.TMgray {
   color: $TMgray;
   background-color: white;
   @include coloredbutton($TMgray); }

.TMred {
   color: $TMred;
   background-color: white;
   @include coloredbutton($TMred); }

.TMpurple {
   color: $TMpurple;
   background-color: white;
   @include coloredbutton($TMpurple); }

.btnExit {
   display: flex;
   align-items: center;
   justify-content: center;
   background-color: white;
   width: 2.5rem;
   height: 2.5rem;
   border-radius: 24px;
   border: none ;
   transition: background 0.8s, color 0.8s;
   margin-bottom: 0.5rem;
   font-size: 1.5rem; }

