   .div13 {
      grid-area: 2 / 1 / 3 / 2; }
   .div14 {
      grid-area: 2 / 2 / 3 / 3; }
   .div1 {
      grid-area: 1 / 1 / 2 / 3; }
   .div2 {
      grid-area: 1 / 3 / 2 / 5; }
   .div3 {
      grid-area: 2 / 1 / 3 / 3; }
   .div4 {
      grid-area: 2 / 3 / 3 / 5; }
   .div5 {
      grid-area: 3 / 1 / 4 / 3; }
   .div6 {
      grid-area: 3 / 3 / 4 / 5; }
   .div7 {
      grid-area: 4 / 2 / 5 / 4; } }

@media (min-width: 992px) {
   .div11 {
      grid-area: 1 / 1 / 2 / 2; }
   .div12 {
      grid-area: 1 / 2 / 2 / 3; }
   .div13 {
      grid-area: 1 / 3 / 2 / 4; }
   .div14 {
      grid-area: 2 / 2 / 3 / 3; }
   .div1 {
      grid-area: 1 / 1 / 2 / 3; }
   .div2 {
      grid-area: 1 / 3 / 2 / 5; }
   .div3 {
      grid-area: 1 / 5 / 2 / 7; }
   .div4 {
      grid-area: 2 / 1 / 3 / 3; }
   .div5 {
      grid-area: 2 / 3 / 3 / 5; }
   .div6 {
      grid-area: 2 / 5 / 3 / 7; }
   .div7 {
      grid-area: 3 / 3 / 4 / 5; } }

@media (min-width: 1324px) {
