   .div13 {
      grid-area: 1 / 3 / 2 / 4; }
   .div14 {
      grid-area: 1 / 4 / 2 / 5; }

   .div1 {
      grid-area: 1 / 1 / 2 / 3; }
   .div2 {
      grid-area: 1 / 3 / 2 / 5; }
   .div3 {
      grid-area: 1 / 5 / 2 / 7; }
   .div4 {
      grid-area: 1 / 7 / 2 / 9; }
   .div5 {
