.animateTop {
    position: absolute;
    transform-origin: left top;
    animation: animatetop 0.4s;
    top: 0px;
    left: 0px; }


@keyframes animatetop {
    from {
        top: +300px;
        opacity: 0; }
    to {
        top : 0;
        opacity: 1; } }
