.inputDate {
    font-family:  Montserrat;
    font-weight: 500;
    margin-bottom: calculateRem(77px*0.7);
    width: calculateRem(666px*0.7);
    display: block;
    border: none;
    padding: 1rem 0;
    border-bottom: solid 1px #A9B2B1;
    transition: all 0.3s cubic-bezier(.64,.09,.08,1);
    background: linear-gradient(to bottom, rgba(255,255,255,0) 96%, #004165 4%);
    background-position: calculateRem(-666px*0.7) 0;
    background-size: calculateRem(666px*0.7) 100%;
    background-repeat: no-repeat;
    color: #004165; }

