.background {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 375px;
    height: 812px;
    background: #A9B2B1 0% 0% no-repeat padding-box;
    border-radius: 0px 0px 0px 80px; }

@mixin navBarBox() {
    position: absolute;
    left: 9px;
    width: 48px;
    height: 48px;
    border-radius: 50px;
    border: 2px solid gray; }

@mixin box($left: null) {
    position: absolute;
    top: 50px;
    left: $left;
    width: 70px;
    height: 77px;
    border: none; }

.youBox {
    @include box(42px);
    opacity: 0.75;
    .youText {
        position: absolute;
        height: 100%;
        top: 55px;
        width: 70px;
        text-align: center;
        font: Bold 11px/14px Montserrat;
        color: #002133; }
    .you {
        @include navBarBox();
        .profileLogo {
            border: 0.2rem gray solid;
            background: center no-repeat;
            background-size: 100% auto ;
            background-color: gray;
            border-radius: 50px;
            height: 48px;
            width: 48px;
            position: relative;
            float: left;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); } } }

.nextWeekBox {
    @include box(152px);
    opacity: 0.75;
    .nextWeekText {
        position: absolute;
        height: 100%;
        top: 55px;
        width: 70px;
        text-align: center;
        font: Bold 11px/14px Montserrat;
        color: #002133; }
    .nextWeek {
        @include navBarBox();
        .nextWeekLogo {
            position: relative;
            float: left;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); } } }

.summaryBox {
    @include box(263px);
    opacity: 0.75;
    .summaryText {
        position: absolute;
        height: 100%;
        top: 55px;
        width: 70px;
        text-align: center;
        font: Bold 11px/14px Montserrat;
        color: #002133; }
    .summary {
        @include navBarBox();
        border: 2px solid #004165;
        .summaryLogo {
            position: relative;
            float: left;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); } } }

.menu {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 375px;
    height: 156px;
    background: white;
    border-radius: 0px 0px 0px 80px; }

.summaryTitle {
    position: absolute;
    top: 177px;
