.joinUs {
    position: absolute;
    top: 60px;
    left: 147px;
    width: 81px;
    height: 32px;
    background: #772432 0% 0% no-repeat padding-box;
    border-radius: 30px;
    text-align: center;
    padding-top: 5px;
    font: Regular 12px/26px Montserrat;
    letter-spacing: 0.05px;
    color: #FFFFFF;
    border: none;
    outline: none; }


